body {
  margin: 0;
  padding: 0;
  min-width: 320px;
  font-family: "PT Mono", "Arial", sans-serif;
  color: #303030;
  font-size: 16px;
  line-height: 22px;
  font-style: normal;
  background-color: #1b2931;
  background-image: url("../img/body-background.png");
  background-repeat: repeat-x;
  background-size: auto 878px;
  background-position: 50% 0; }

img {
  max-width: 100%;
  height: auto; }

@font-face {
  font-family: "PT Mono";
  src: url("../fonts/pt-mono-regular.woff2") format("woff2"), url("../fonts/pt-mono-regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: "Roboto Condensed";
  src: url("../fonts/roboto-condensed-regular.woff2") format("woff2"), url("../fonts/roboto-condensed-regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: "Roboto Condensed";
  src: url("../fonts/roboto-condensed-700.woff2") format("woff2"), url("../fonts/roboto-condensed-700.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

.wrapper {
  max-width: 1440px;
  margin: 0 auto; }

