.page-header {
  color: #ffffff;
  max-width: 1440px;
  margin: 0 auto;
  padding: 0 15px;
  padding-top: 33px;
  box-sizing: border-box;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center; }
  @media (max-width: 1023px) {
    .page-header {
      padding: 0 25px; } }
  @media (max-width: 768px) {
    .page-header {
      padding: 0 17px; } }

.page-header__logo {
  width: 200px;
  margin: 0; }

.page-header__list {
  width: 441px;
  margin: 0; }

.page-header__link {
  color: #ffffff;
  text-decoration: none;
  line-height: 18px; }

.page-header__button {
  font-family: "Roboto Condensed", "Arial", sans-serif;
  color: #ffffff;
  text-decoration: none;
  text-align: center;
  line-height: 16px;
  display: block;
  width: 180px;
  min-height: 45px;
  box-sizing: border-box;
  padding: 0 12px;
  padding-top: 13px;
  padding-bottom: 8px;
  border: 1px solid #ffffff;
  border-radius: 5px; }

.page-header__contacts {
  width: 650px;
  margin: 0;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between; }

.page-header__link--mail {
  margin-right: 88px;
  padding-left: 39px;
  position: relative; }
  .page-header__link--mail::before {
    content: "";
    position: absolute;
    background-image: url("../img/envelope_icon.svg");
    background-repeat: no-repeat;
    background-size: 19px 14px;
    background-position: 0 0;
    width: 19px;
    height: 14px;
    top: 0;
    left: 0; }

.page-header__link--tel {
  padding-left: 34px;
  position: relative; }
  .page-header__link--tel::before {
    content: "";
    position: absolute;
    background-image: url("../img/phone-call_icon.svg");
    background-repeat: no-repeat;
    background-size: 18px 18px;
    background-position: 0 0;
    width: 18px;
    height: 18px;
    top: 0;
    left: 0; }

@media (max-width: 1023px) {
  .page-header {
    max-width: 1023px;
    padding-top: 20px; }
  .page-header__link--hidden {
    display: none; }
  .page-header__button {
    display: none; }
  .page-header__list {
    width: 157px;
    min-height: 24px;
    margin: 0;
    display: flex;
    justify-content: flex-end;
    align-items: center; }
  .page-header__link {
    margin-left: 33px; }
  .page-header__link--mail {
    margin: 0;
    width: 25px;
    height: 19px;
    margin-right: 0;
    padding-left: 0; }
    .page-header__link--mail::before {
      background-size: 25px 19px;
      width: 25px;
      height: 19px; }
  .page-header__contacts {
    width: 157px; }
  .page-header__link--tel {
    width: 24px;
    height: 24px;
    padding-left: 0; }
    .page-header__link--tel::before {
      background-size: 24px 24px;
      width: 24px;
      height: 24px; } }

@media (max-width: 768px) {
  .page-header {
    max-width: 767px;
    padding-top: 17px; }
  .page-header__logo {
    width: 38px; }
  .page-header__logo-image {
    width: 38px;
    height: 38px; } }

.main-info {
  margin-top: 173px;
  color: #ffffff;
  position: relative;
  min-height: 793px; }
  .main-info::before {
    content: "";
    position: absolute;
    background-image: url("../img/main-background.png");
    background-repeat: no-repeat;
    background-size: 705px 515px;
    background-position: 0 0;
    width: 705px;
    height: 515px;
    left: 777px;
    top: 0;
    overflow: hidden; }

.main-info__title {
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-style: normal;
  font-weight: bold;
  font-size: 80px;
  line-height: 94px;
  text-transform: uppercase;
  margin: 0;
  padding-top: 58px;
  padding-left: 15px;
  width: 777px; }

.main-info__message {
  width: 683px;
  font-size: 22px;
  line-height: 34px;
  padding-left: 109px;
  margin: 0;
  margin-top: 31px;
  position: relative; }
  .main-info__message::before {
    content: "";
    position: absolute;
    width: 66px;
    height: 2px;
    left: 15px;
    top: 14px;
    background-color: #ffffff; }

.main-info__button {
  display: block;
  margin-left: 109px;
  margin-top: 37px;
  width: 407px;
  padding: 17px 28px;
  box-sizing: border-box;
  background-color: #00b2ff;
  border-radius: 5px;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-weight: bold;
  line-height: 19px;
  text-align: center; }

.main-info__scroll {
  margin: 0;
  font-size: 14px;
  line-height: 16px;
  opacity: 0.5;
  transform: rotate(-90deg);
  width: 93px; }

@media (max-width: 1023px) {
  .main-info {
    margin-top: 41px; }
    .main-info::before {
      background-size: 465px 340px;
      width: 465px;
      height: 340px;
      left: 510px; }
  .main-info__scroll {
    display: none; }
  .main-info__title {
    padding-top: 75px;
    padding-left: 90px;
    width: 470px;
    font-size: 60px;
    line-height: 70px; }
  .main-info__message {
    font-size: 18px;
    width: 356px;
    padding-left: 140px; }
    .main-info__message::before {
      width: 33px;
      left: 91px; } }

.advantages {
  background-color: #ffffff; }

