.main-info {
  margin-top: 173px;
  color: #ffffff;
  position: relative;
  min-height: 793px;

  &::before {
    content: "";
    position: absolute;

    background-image: url("../img/main-background.png");
    background-repeat: no-repeat;
    background-size: 705px 515px;
    background-position: 0 0;

    width: 705px;
    height: 515px;
    left: 777px;
    top: 0;

    overflow: hidden;
  }
}

.main-info__title {
  font-family: $font-Roboto;
  font-style: normal;
  font-weight: bold;
  font-size: 80px;
  line-height: 94px;
  text-transform: uppercase;

  margin: 0;
  padding-top: 58px;
  padding-left: 15px;
  width: 777px;
}

.main-info__message {
  width: 683px;

  font-size: 22px;
  line-height: 34px;
  padding-left: 109px;
  margin: 0;
  margin-top: 31px;

  position: relative;

  &::before {
    content: "";
    position: absolute;
    width: 66px;
    height: 2px;
    left: 15px;
    top: 14px;

    background-color: #ffffff;
  }
}

.main-info__button {
  display: block;
  margin-left: 109px;
  margin-top: 37px;
  width: 407px;
  padding: 17px 28px;
  box-sizing: border-box;
  background-color: $blue;
  border-radius: 5px;

  font-family: $font-Roboto;
  font-weight: bold;
  line-height: 19px;
  text-align: center;
}

.main-info__scroll {
  margin: 0;

  font-size: 14px;
  line-height: 16px;

  opacity: 0.5;
  transform: rotate(-90deg);
  width: 93px;
}

@media (max-width: $desktop-width) {
  .main-info {
    margin-top: 41px;

    &::before {
      background-size: 465px 340px;
      width: 465px;
      height: 340px;

      left: 510px;
    }
  }

  .main-info__scroll {
    display: none;
  }

  .main-info__title {
    padding-top: 75px;
    padding-left: 90px;
    width: 470px;
    font-size: 60px;
    line-height: 70px;
  }

  .main-info__message {
    font-size: 18px;
    width: 356px;
    padding-left: 140px;

    &::before {
      width: 33px;
      left: 91px;
    }
  }
}
